body{
	font-family:Tahoma,Geneva,sans-serif;
	font-weight:400
	}

ol{margin:0;padding:0;list-style-type:none;width:100%}

.menu{
    background-color: #a89344;
	width:100%;
	padding:0 16px;
	display:block;
    position:relative;
    flex-grow: 10;
	}

.menu > ol:first-child{
	width:100%;
	display:flex;
	position:relative;
	flex-flow:row wrap;
	justify-content:center;
	}

.menu-item{
	width:150px;
	position:relative;
	-webkit-perspective:100%;
	        perspective:100%;
	cursor:pointer
	}

.menu-item a{
	width:100%;
	padding:16px 0;
	display:block;
	position:relative;
	color:#fff;
	border-radius:4px;
	-webkit-transition:all .5s ease;
	transition:all .5s ease;
	-webkit-transform-style:preserve-3d;
	        transform-style:preserve-3d
	}

.menu-item a:before{
	width:100%;
	padding:16px 0;
	display:block;
	position:absolute;
	color:white;
	border-radius:4px
	}

.menu > ol:first-child > .menu-item{display:block}

.menu > ol:first-child > .menu-item > a
	{background:#102846;box-shadow:0 8px 6px -6px #595959}

.menu > ol:first-child > .menu-item:nth-of-type(2) > a,
.menu > ol:first-child > .menu-item:nth-of-type(4) > a
	{border-left:1px solid #fff;border-right:1px solid #fff}

.menu > ol:first-child > .menu-item > a:before
	{background:#a89344;bottom:90%;-webkit-transform:rotateX(-90deg) translate3d(0,0,75px);transform:rotateX(-90deg) translate3d(0,0,75px)}

.menu > ol:first-child > .menu-item:nth-of-type(1) > a:before{content:"Home"}
.menu > ol:first-child > .menu-item:nth-of-type(2) > a:before{content:"About"}
.menu > ol:first-child > .menu-item:nth-of-type(3) > a:before{content:"Tests"}
.menu > ol:first-child > .menu-item:nth-of-type(4) > a:before{content:"Jobs"}
.menu > ol:first-child > .menu-item:nth-of-type(5) > a:before{content:"Contact"}

.menu > ol:first-child > .menu-item:nth-of-type(3) > a:after,
.menu > ol:first-child > .menu-item:nth-of-type(4) > a:after{
	content:"\25BE";
	width:100%;
	padding:30px 0 0 0;
	top:0;
	display:block;
	position:absolute;
	color:#fff;
	font-size:20px;
	text-align:center
	}

.menu > ol:first-child > .menu-item:hover > a{-webkit-transform:rotateX(90deg);transform:rotateX(90deg)}

.menu > ol:first-child > .menu-item:nth-of-type(3):hover > a:after,
.menu > ol:first-child > .menu-item:nth-of-type(4):hover > a:after
	{content:"\25B4";color:#000;-webkit-transform:rotateX(-90deg) translate3d(0,0,75px);transform:rotateX(-90deg) translate3d(0,0,75px)}

.sub-menu{
	background:#fff;
	width:100%;
	top:51px;
	display:none;
	position:absolute;
	z-index:99;
	border-top:1px solid #fff
	}

/* if adding anything in the submenu this needs to be added to transform it. */
.sub-menu > .menu-item:nth-of-type(1){-webkit-animation:fadein-down .5s .2s ease both;animation:fadein-down .5s .2s ease both}
.sub-menu > .menu-item:nth-of-type(2){-webkit-animation:fadein-down .7s .2s ease both;animation:fadein-down .7s .2s ease both}
.sub-menu > .menu-item:nth-of-type(3){-webkit-animation:fadein-down .9s .2s ease both;animation:fadein-down .9s .2s ease both}
.sub-menu > .menu-item:nth-of-type(4){-webkit-animation:fadein-down .9s .2s ease both;animation:fadein-down .9s .2s ease both}

@-webkit-keyframes fadein-down{
	from{display:none;opacity:0;-webkit-transform:translateY(-10px);transform:translateY(-10px)}
	to{display:block;opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}
	}

@keyframes fadein-down{
	from{display:none;opacity:0;-webkit-transform:translateY(-10px);transform:translateY(-10px)}
	to{display:block;opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}
	}

.sub-menu > .menu-item > a{background:#a89344}

.sub-menu > .menu-item:nth-of-type(2) > a
	{border-top:1px solid #fff;border-bottom:1px solid #fff}

.sub-menu > .menu-item > a:before
	{background:#102846;top:0}

/* if adding anything in the submenu this needs to be added to transform it. */
.menu > ol:first-child > .menu-item:nth-of-type(3) > .sub-menu > .menu-item > a:before{-webkit-transform:rotateY(-90deg) translate3d(0,0,75px);transform:rotateY(-90deg) translate3d(0,0,75px)}
.menu > ol:first-child > .menu-item:nth-of-type(3) > .sub-menu > .menu-item:nth-of-type(1) > a:before{content:"JavaScript Test"}
.menu > ol:first-child > .menu-item:nth-of-type(3) > .sub-menu > .menu-item:nth-of-type(2) > a:before{content:"Python Test"}
.menu > ol:first-child > .menu-item:nth-of-type(3) > .sub-menu > .menu-item:nth-of-type(3) > a:before{content:"CSS Test"}
.menu > ol:first-child > .menu-item:nth-of-type(3) > .sub-menu > .menu-item:nth-of-type(4) > a:before{content:"React Test"}
.menu > ol:first-child > .menu-item:nth-of-type(3) > .sub-menu > .menu-item:hover > a{-webkit-transform:rotateY(90deg);transform:rotateY(90deg)}

.menu > ol:first-child > .menu-item:nth-of-type(4) > .sub-menu > .menu-item > a:before{-webkit-transform:rotateY(90deg) translate3d(0,0,75px);transform:rotateY(90deg) translate3d(0,0,75px)}
.menu > ol:first-child > .menu-item:nth-of-type(4) > .sub-menu > .menu-item:nth-of-type(1) > a:before{content:"Employers"}
.menu > ol:first-child > .menu-item:nth-of-type(4) > .sub-menu > .menu-item:nth-of-type(2) > a:before{content:"Some Dude"}
.menu > ol:first-child > .menu-item:nth-of-type(4) > .sub-menu > .menu-item:nth-of-type(3) > a:before{content:"Info"}
.menu > ol:first-child > .menu-item:nth-of-type(4) > .sub-menu > .menu-item:hover > a{-webkit-transform:rotateY(-90deg);transform:rotateY(-90deg)}

.menu > ol:first-child > .menu-item:hover > .sub-menu{display:block}

@media screen and (min-width:321px) and (max-width:799px){
	.menu{width:320px;margin:auto}
	}

@media (max-width:799px){
	.menu > ol:first-child > .menu-item{width:100%}
	.menu > ol:first-child > .menu-item > a{box-shadow:none}
	
	.menu > ol:first-child > .menu-item:nth-of-type(2) > a,
	.menu > ol:first-child > .menu-item:nth-of-type(4) > a
		{border:1px solid #fff;border-left:none;border-right:none}
	
	.sub-menu > .menu-item{margin:auto}
	}
